.navtab {
    background-color:#111;
    color: #818181;
    font-size: 18px;
    /* transition: .3s; */
    white-space: nowrap;
    text-transform: uppercase;
    padding-top: 60px;
    /* padding-top: 150px;   */
}

.drawer{
    transition: 1s;
}


/* This controls the list component that contains the icons and the labels */
.tab {
    list-style-type: none;
    margin-bottom: 30px;
    padding-left:30px;
}

/* We use the unordered list component to make sure when we hover, he colour changes and the pointer too */
.tab-container :hover {
    color: #f1f1f1;
    cursor: pointer;   
}



/* Features of the icon */
.material-icons {
    position:fixed;
    left:7.5px;
    text-align: left;
    
}
